<template>
  <div class="askDetall">
    <!-- <mt-header :title="title">
      <mt-button  icon="back" slot="left" @click="handleBack">返回</mt-button>
    </mt-header> -->

    <section class="header_search">
      <!-- <div class="user" @click="gotoAddress('myinfo',false)"><img :src="getUserAvatar"></div> -->
      <mt-button  icon="back" slot="left" @click="handleBack"></mt-button>
      <div class="search">
        <img src="../../images/myinfo/search_not.png">
        <input type="search" placeholder="房产知识有疑问？来搜搜吧~" v-model="searchStr" />
      </div>
      <i @click="gotoSearch()">搜索</i>
    </section>

    <section class="ask" v-if="data">
      <h2>{{data.q_title}}</h2>
      <span class="user">
        <img :src="getUserAvatar" />
        <span>
          <em>{{data.trueName}}</em>
          <i>发布于 {{data.q_sendtime | formatDate}}</i>
        </span>
        <div v-if="!isFollow" @click="follow()" class="btn">+关注</div>
        <div v-if="isFollow" @click="follow()" class="active">取消关注</div>

      </span>
      <p><!--<img src="../../images/home/hot_icon.png">-->{{data.q_content}}</p>
      <div class="q_img" v-if="data.imgs">
        <li v-for="item in getQimg" @click="showQImg(item)"><img :src="item"></li>
      </div>
      <div class="bottom">
        <!-- <span class="R"><em>{{data.trueName}}</em><i>|</i><em>{{data.q_sendtime | formatDate}}</em></span>
        <span class="follow">
          <em v-if="!isFollow"  @click="follow()" class="btn">
            关注<i>|</i>{{data.q_cang}}</em>
          <em v-if="isFollow">已关注<i>|</i>{{data.q_cang}}</em>
        </span>
        <span><img src="../../images/home/A_degree_icon.png"><em>{{data.q_replys}}</em></span>
        -->
        <span @click="gotoAnswer"><img src="../../images/home/add_answer.png"><em>添加回答</em></span>
      </div>
    </section>
    <!-- 10线条 -->
    <div class="lise_10"></div>

    <!-- 回答列表 -->
    <section class="answerList" v-if="data">
      <h2>共{{data.answer.length}}个回答</h2>

      <!--
      :itemData 数据源
      :userid 提问用户id
      @showQImg 图片浏览事件
      @feedback 赞 与 驳事件
      @adopt  采纳为最佳答案事件
      -->

      <li v-for="item in trueAnswer" data="trueAnswer">
        <AList
          :itemData = "item"
          :userid = "data.id"
          :ask = "data"
          @feedback = "feedback"
          @adopt = "adopt"
          @showQImg = "showQImg" >
        </AList>
      </li>

      <li v-for="item in vipAnswer" data="vipAnswer">
        <AList
          :itemData = "item"
          :userid = "data.id"
          :ask = "data"
          @feedback = "feedback"
          @adopt = "adopt"
          @showQImg = "showQImg" >
        </AList>
      </li>
      <li v-for="item in answer" data="answer">
        <AList
          :itemData = "item"
          :userid = "data.id"
          :ask = "data"
          @feedback = "feedback"
          @adopt = "adopt"
          @showQImg = "showQImg" >
        </AList>
      </li>
    </section>

    <!-- 暂时隐藏底部的回答工具栏先保留着吧怕那天又要了  你懂得   -->
    <!-- 底部的回答工具栏 -->
    <section  v-if="false" class="bottom_tool" :class="isShowTool ? 'fixed_200' : 'fixed_0'">
      <div class="tool">
        <i class="imgBtn" @click="showTool()">
          <img src="../../images/main/img_btn.png">
          <em v-if="replys.img.length && !isShowTool">{{replys.img.length}}</em>
        </i>
        <input :placeholder="isLogin ?'输入回答' :'请先登录'" v-model="replys.nr" />
        <i class="comit" @click="postReplys()">提交</i>
      </div>
      <div class="upImg">
        <div class="imgWrap">
          <!-- 要上传的图片 -->
          <div class="img" v-for="item,i in replys.img">
            <img :src="item" @click="showQImg(item)">
            <i @click="delUpImg(i)"><img src="../../images/main/img_del.png"></i>
          </div>
          <!-- 图片上传 -->
          <div class="img" v-if="replys.img.length < 3">
            <upload flag="'thumbnail'" @complete="uploadComplete" @progress="uploadProgress">
                  <img src="../../images/main/add_img.png">
            </upload>
          </div>
        </div>
        <p v-if="replys.img.length == 0">一共可以选择3张图片</p>
        <p v-if="replys.img.length">已选{{replys.img.length}}张，还可选择{{3 - replys.img.length}}张</p>
      </div>
    </section>

    <section class="PopImg" v-if="isShowPopImg" @click="showPopImg(0)">
      <div><img :src="popImgUrl"></div>
    </section>

    <!-- 65线条 -->
    <div v-if="false"  :class="isShowTool ? 'lise_240' : 'lise_40'"></div>
  </div>
</template>

<script>
import { Toast,Indicator,MessageBox} from 'mint-ui'
import {getStore,imgBaseUrl,upImgBaseUrl,getUserAvatar} from '../../components/common/mUtils'
import upload from '../../components/common/upload'
import AList from './children/AList'
export default {
  data(){
    return{
      imgBaseUrl,
      upImgBaseUrl,
      userToken:'',
      isLogin:false,
      data:null,
      answer:[],
      trueAnswer:[],
      vipAnswer:[],
      searchStr:'',
      qid:'',
      title:'',
      isShowTool:false,
      // 回答的参数
      replys:{
        nr:'',
        qid:'',
        imgs:'',
        img:[]
      },
      popImgUrl:'',
      isShowPopImg:false,
      isFollow:false,
    }
  },
  props:['itemData'],
  filters:{
    getAvatarPath(path){
      return '//img.07358.com'+path
    }
  },
  created(){
    this.userID = getStore('userID')
    this.userToken = getStore('userToken')
    if (this.userID) {
      this.isLogin = true
    }
  },
  mounted(){
    //this.userToken = 'MlDiUbv-FiE4eu23HIJ9u9pv9MwkqCmo'
    this.qid = this.$route.query.qid
    this.replys.qid = this.qid
    this.getData()
    if (this.isLogin) {
      this.getIsFollow()
    }
  },
  components:{
    upload,
    AList
  },
  computed:{
    getQimg(){
      let imgs = this.data.imgs; //这是一字符串
      return imgs.split(","); //字符分割
    },
    getUserAvatar:function(){
      return getUserAvatar(this.data.avatarPath)
      // if(this.data.avatarPath){
      //   // http://tu.07358.com
      //   let indexOf = this.data.avatarPath.indexOf('http://tu.07358.com')
      //   if(indexOf != -1){
      //     return this.data.avatarPath
      //   }else {
      //     return imgBaseUrl + this.data.avatarPath
      //   }
      // }else {
      //   return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      // }
    }
  },
  methods: {
    handleBack(){
      this.$router.go(-1);
    },
    gotoSearch(){
      let str = encodeURI(encodeURI(this.searchStr));
      this.$router.push({path: '/search', query:{keyStr:str,type:1}})
    },
    showTool(){
      if (!this.validateLogin()) {
        return
      }
      this.isShowTool = !this.isShowTool
    },
    gotoAnswer(){
      if (!this.validateLogin()) {
        return
      }
      this.$router.push({path:'answer',query:{qid:this.qid }})
    },
    getData(){
      // Indicator.open('加载中....');
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/detail?id='+this.qid
      // console.log(getUrl)
      this.axios.get(getUrl)
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.data = response.data.result
          this.title = this.data.q_title
          this.answer = []
          this.vipAnswer=[]
          this.trueAnswer=[]
          this.data.answer.forEach((item) => {
            console.log(item.userh.ifzj+'-----'+item.ifgood)
            if (item.userh.ifzj == '1') {
              this.vipAnswer.push(item)
            }else if (item.ifgood == '1'){
              this.trueAnswer.push(item)
            }else {
              this.answer.push(item)
            }
          })
        }
        // Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    getIsFollow(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/ifgz?id='+this.qid+'&token='+this.userToken
      this.axios.get(getUrl)
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.isFollow = response.data.result == 1 ? true : false
        }
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    postReplys(){
      if (!this.validateFormData()) {
        return
      }
      Indicator.open('提交中...');
      let imgsUrls = ''
      this.replys.img.forEach((item) =>{
        imgsUrls = imgsUrls == '' ? item : imgsUrls+','+item
      })
      let params = {
        'nr':this.replys.nr,
        'qid':this.replys.qid,
        'imgs':imgsUrls,
      }

      // 发起请求
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/huida?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(params))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.replys.nr = ''
          this.replys.imgs = ''
          this.replys.img = []
          this.isShowTool = 0
          setTimeout(() => {
            this.getData()
          }, 800);
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },// 上传图片过程中的回调
    uploadProgress (progress, flag) {
        //这里可以通过回调的flag对不同上传域做处理
        this.progress = progress < 100 ? progress : 0;
        console.log(this.progress)
    },
    // 上传图片完成的回调
    uploadComplete(status, result, flag) {
        if (status == 200) { //
          console.log(result.key)
          let imgUrl = upImgBaseUrl + result.key //七牛域名 + 返回的key 组成文件url
          this.replys.img.push(imgUrl)
        } else {
            //失败处理
        }
    },
    // 表单数据验证
    validateFormData(){
      if (!this.validateLogin()) {
        return
      }

      if (this.replys.nr == '') {
        Toast('请输入您的答案！');
        return
      }
      return true
    },
    // 参数转化成表单对象
    objToformData(obj){
      // let params = new URLSearchParams()
      let params = new FormData()
      Object.keys(obj).forEach( (key, i) => {
        params.append(key, obj[key]);
      });
      return params;
    },
    // 显示隐藏 PopImg
    showPopImg(isShow){
      this.isShowPopImg = isShow
    },
    showQImg(item){
      this.isShowPopImg = 1
      this.popImgUrl = item
    },
    delUpImg(idx){
      let array = []
      this.replys.img.forEach((item, i) =>{
        if (idx != i) {
          array.push(item)
        }
      })
      this.replys.img = array
    },
    validateLogin(){
      if (!this.isLogin) {
        MessageBox.confirm('请先登录,确定登录吗？').then(({ action }) => {
          this.$router.push('/login')
        }, response => {
        });
        return false
      }else {
        return true
      }
    },
    // 对回答的反馈  赞  驳
    feedback(fk_p){
      if (!this.validateLogin()) {
        return
      }

      Indicator.open('提交中...');
      let params = {
        'aid':fk_p.r_id,
        'jilu':fk_p.type,
      }
      // 发起请求 'CYKVUJ8teDegtOaHabyCwvYIHsfYRLh0'
      // let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(params))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          setTimeout(() => {
            this.getData()
          }, 800);
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    // 采纳为最佳答案
    adopt(params){
      if (!this.validateLogin()) {
        return
      }
      Indicator.open('提交中...');
      let param = {
        'aid':params.r_id,
      }
      // let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/caina?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(param))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          setTimeout(() => {
            this.getData()
          }, 800);
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    follow(){
      if (!this.validateLogin()) {
        return
      }
      Indicator.open('提交中...');
      let param = {
        'itemid':this.qid,
      }
      // let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/gzwenti?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(param))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.getIsFollow()
          this.getData()
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    }
  }

}

</script>

